import { useI18n } from 'vue-i18n'
import { watch, ref } from 'vue'
import moment from 'moment'

export default function useDate (date = new Date(), format = 'DD MMM YYYY') {
  const { locale } = useI18n()
  const formatDate = ref(moment(date).format(format))
  watch(locale, () => {
    formatDate.value = moment(date).format(format)
  })

  return formatDate
}
