<template>
  <Icon
    name="arrow-left"
    class="back-btn"
    @click="goBack"
  />
</template>

<script>
import { useRouter } from 'vue-router'
import Icon from '@/components/Icon/Icon'

export default {
  name: 'BackBtn',
  components: { Icon },
  setup () {
    const router = useRouter()

    const hasHistory = () => window.history.length > 2

    const goBack = () => {
      if (hasHistory()) {
        router.go(-1)
      } else {
        router.push('/')
      }
    }

    return { goBack }
  }
}
</script>

<style lang="scss">
  .back-btn {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    cursor: pointer;

    &:hover {
      background: #F0F0F0;
    }
  }
</style>
